$textcolor: #1f1f1f;

* {
    margin: 0;
    padding: 0;
}

.flex {
    display: flex;
}

#nav {
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    margin-right: 2vh;
}

.nav-me {
    font-size: 3vh;
    font-weight: bold;
    letter-spacing: 2px;
    color: $textcolor;
}

.nav-links {
    flex-direction: row;
    align-items: center;
    gap: 3vh;
}

.nav-btn {
    font-size: 2vh;
    font-weight: bold;
    background: transparent;
    border: none;
    outline: none;
    cursor: pointer;
    color: $textcolor;
    text-decoration: none;
}

#home {
    justify-content: center;
    gap: 5vh;
    height: 80vh;
}

.home-info {
    width: 30%;
    text-align: center;
    margin: auto 0;
    color: $textcolor;
}

.home-info p {
    width: 100%;
    margin: 1vh auto;
    text-align: left;
}

.home-links-section {
    justify-content: center;
    gap: 2vh;
    height: fit-content;
    width: 100%;
    margin: 2vh 0;
}

.home-skills-section {
    font-weight: bold;
    width: 80%;
    color: $textcolor;
    margin: 0 auto;
    height: fit-content;
}

.home-stack-section {
    margin: 1vh auto;
    justify-content: center;
    gap: 1vh;
}

.home-badge-icon {
    height: 30px;
    aspect-ratio: 1/1;
    transition-duration: 0.3s;
    cursor: pointer;
}

.home-badge-icon:hover {
    scale: 1.2;
}

#home-photo {
    height: 400px;
    width: 400px;
    background-image: url("../assets/home/me.png");
    background-size: 400px;
    animation: 15s cool-photo-border infinite alternate;
    position: relative;
    margin: auto 0;
    border: 5px solid rgba(255, 255, 255, 0.5);
}

#about {
    justify-content: center;
    align-items: baseline;
    gap: 5vh;
    width: 100%;
}

#about-decor-elem {
    width: 400px;
    aspect-ratio: 3/4;
    background: teal;
    position: relative;
    margin: auto 0;
    border-radius: 20px;
}

.about-me {
    width: 300px;
    text-align: center;
    flex-direction: column;
    justify-content: space-evenly;
    color: $textcolor;
    margin: 1vh 0;
}

.about-me > p {
    text-align: start;
    margin: 2vh 0;
}

.project-stack {
    text-align: center;
    letter-spacing: 2px;
}


#portfolio {
    height: fit-content;
    width: initial;
    flex-direction: column;
    align-items: center;
    margin: 5vh 0;
}

.project {
    width: calc(800px + (12px * 4));
    height: fit-content;
    align-items: center;
    margin: 12px 0;
    border-radius: 18px;
    border: 5px solid rgba(128, 128, 128, 0.5);
}

.project-promo {
    margin: 12px;
    height: calc(300px + 30px);
    width: 50%;
    border-radius: 12px;
    flex-direction: column;
    justify-content: space-between;
}

.project-promo > img {
    height: 100%;
    width: 100%;
}

.project-links {
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}

.project-links > a {
    cursor: pointer;
}

.project-github-link {
    height: 30px;
    width: 30px;
    background: url("https://cdn.jsdelivr.net/gh/devicons/devicon@latest/icons/github/github-original.svg");
}

.project-website-link {
    color: $textcolor;
    font-weight: bold;
}

.project-descrip {
    background-color: rgba(255, 255, 255, 0.5);
    width: 45%;
    height: 300px;
    text-align: center;
    flex-direction: column;
    color: $textcolor;
    justify-content: space-evenly;
}

.project-descrip>p {
    background-color: rgba(255, 255, 255, 0.5);
    width: 80%;
    color: $textcolor;
    text-align: left;
    margin: 0 auto;
}

#contact {
    flex-direction: row;
    justify-content: center;
    align-items: baseline;
    color: $textcolor;
    gap: 2vh;
}

.contacts-section {
    flex-direction: column;
    gap: 1vh;
}

.contact {
    flex-direction: row;
    align-items: center;
    font-weight: bold;
    color: $textcolor;
    height: 30px;
}

.contact img {
    height: 30px;
    width: 30px;
    margin: 0 10px;
}

@media (max-width: 500px) {

    /* mobile navbar style */
    .nav-btn {
        height: 50px;
        width: 50px;
    }

    .nav-btn p {
        display: none;
    }

    #id-home {
        background: url("../assets/navbar/icons8-home-50.png");
    }

    #id-about {
        display: none;
    }

    #id-portfolio {
        background: url("../assets/navbar/icons8-view-quilt-50.png");
    }

    #id-contact {
        background: url("../assets/navbar/icons8-contacts-50.png");
    }

    /* mobile home section style */
    #home {
        flex-direction: column-reverse;
    }

    .home-info {
        width: 380px;
        margin: auto;
    }

    #home-photo {
        height: 400px;
        width: 400px;
        background-image: url("../assets/home/me.png");
        background-size: 400px;
        animation: 15s cool-photo-border infinite alternate;
        position: relative;
        margin: 3vh auto 1vh;
        border: 5px solid rgba(255, 255, 255, 0.5);
    }

    /* mobile about section style */
    #about {
        width: 380px;
        margin: 2vh auto;
    }

    #about h1 {
        text-align: center;
    }

    #about p {
        margin: 1vh 0;
    }

    /* mobile portfolio section style */
    #portfolio {
        width: calc(100% - 2vh * 2);
        margin: 2vh;
        display: flex;
        flex-direction: column;
        gap: 1vh;
    }

    #portfolio h1 {
        text-align: center;
    }

    .project {
        background: rgba(129, 129, 129, 0.3);
        width: calc(100% - 12px * 2);
        margin: 0 auto;
        padding: 12px;
        border-radius: 18px;
    }

    .project-promo {
        background: rgba(129, 129, 129, 0.3);
        border-radius: 12px;
    }

    .progect-img {
        width: 100%;
        aspect-ratio: 16/9;
        border-radius: 12px;
        border: none;
    }

    .project-links {
        width: 100%;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
    }

    .project-github-link {
        height: 30px;
        width: 30px;
        background: url("https://cdn.jsdelivr.net/gh/devicons/devicon@latest/icons/github/github-original.svg");
    }

    .project-website-link {
        font-weight: bold;
        letter-spacing: 2px;
    }

    .project-descrip,
    .project-descrip p {
        margin: 1vh 0;
    }

    .project-stack {
        text-align: center;
        letter-spacing: 2px;
    }

    /* mobile portfolio section style */
    #contact {
        width: 100%;
        text-align: center;
    }

    .footer-contacts-section {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .contact {
        display: flex;
        align-items: center;
        font-weight: bold;
    }

    .contact img {
        height: 3vh;
        margin-right: 2vh;
    }
}

/* animations */
@keyframes cool-photo-border {

    0%,
    100% {
        border-radius: 50% 50% 60% 70%;
    }

    15% {
        border-radius: 60% 70% 60% 60%;
    }

    30% {
        border-radius: 50% 50% 80% 60%;
    }

    45% {
        border-radius: 60% 70% 60% 60%;
    }

    60% {
        border-radius: 60% 70% 50% 50%;
    }

    75% {
        border-radius: 70% 60% 70% 50%;
    }

    90% {
        border-radius: 60% 70% 50% 50%;
    }
}